import { LoadingOutlined } from '@ant-design/icons';
import classNames from 'classnames';
import React from 'react';
import Style from './Style.module.scss';

const Button: React.FC<any> = (props: {
  className?: string;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
  children?: JSX.Element;
  loading?: boolean;
}) => {
  const { className, children, onClick, loading } = props;
  return (
    <div className={classNames(Style.Button, className, { [Style.Loading]: loading })} onClick={onClick}>
      {children}{' '}
      {loading ? (
        <>
          &nbsp; <LoadingOutlined />
        </>
      ) : null}
    </div>
  );
};
Button.defaultProps = { className: '', children: null, onClick: undefined, loading: false };
export { Button };
